@media only screen and (max-width: 1366px) {
  .movies .movie {
    width: 48% !important;
  }
}

@media only screen and (max-width: 1024px) {
  .movies .movie {
    width: 100% !important;
  }
}

@media only screen and (max-width: 500px) {
  .movies .movie a {
    display: block !important;
  }
  .movie img {
    margin: 0 !important;
  }
  .movie .movie__genres {
    display: inline;
  }
  .movie .movie__summary {
    width: 100% !important;
  }
}

.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
  0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie a {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  color: #adaeb9;
  text-decoration: none;
}

.movie img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-right: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  margin: 5px 0px;
}

.genres__genre {
  padding-right: .6rem;
  font-size: 14px;
}

.movie .movie__summary {
  width: 90%;
}

